arted to realize that there is a concept called nationality - and naturally, my answer became “I’m Chinese”. But as I became more and more mature and gained some experience in the society, the answer to that question becomes more and more blurry - I’m Chinese, and I’m also a "),_c('dynamic-highlight',[_vm._v(" citizen of Shenzhen")]),_vm._v("; I’m from the Chinese culture, yet I’ve gained a taste of the Australian culture from my education in Australia... "),_c('dynamic-highlight',[_vm._v("Mountains of questions about identity just flush into my mind and eventually, I lose the answer to the simple yet complicated question - Who truly am I?")])],1),_c('p',[_vm._v(" When I was young, I had no experience in society at all, so my answer to identity was really simple; yet when I grew up, "),_c('dynamic-highlight',[_vm._v(" I gained some experience in society")]),_vm._v(" and therefore I identified myself as an individual of a collective society - "),_c('dynamic-highlight',[_vm._v(" being a Chinese")]),_vm._v(". This is all driven by experiences - I would never identify myself as an overseas student living in Australia until I stepped on the land of Australia and started my first class here. Therefore, to figure out who I am, as well as who you are, let’s rewind back into our memories - from the very beginning to this very instant - where were you born? Where did you spend your childhood? What’s your educational background? ")],1),_c('subtitle',[_vm._v("Let's hear from Elijah Liu")]),_c('p',[_vm._v(" I perceive China as my hometown because I was born and raised up in Beijing, the capital city located in North China. As we all know, the cultural and linguistic diversity of China endows each region a dialect, and Beijing is not an exception. So, I am always getting recognised as a “Beijingnese” in the overseas Chinese community once a few words tumbled out from my mouth due to my conspicuous accents. ")]),_c('p',[_vm._v(" I came to Australia for study in 2017 and had been "